import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);

export default new Router({
	//mode: 'history',
	scrollBehavior(to, from, savePosition) {
		if(savePosition){
			return savePosition
		}else{
			return { x: 0, y: 0 }
		}
	},
	routes: [
		{path: '*', redirect: '/privacy'},
		//{path: '/agreement', name: 'Agreement', component: resolve => require(['@/pages/agreement'], resolve), meta: {title: 'CorDx说用户隐私政策', requireAuth: true}},  //用户协议
		{path: '/privacy', name: 'Privacy', component: resolve => require(['@/pages/privacy'], resolve), meta: {title: 'CorDx说用户隐私政策', requireAuth: true}},  //隐私政策
	]
})